.benfict-container {
    background-color: #f9f9f9; /* لون خلفية فاتح */
    padding: 40px 20px; /* هوامش داخلية */
    border-radius: 8px; /* زوايا دائرية */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* ظل خفيف */
  }

  .title {
    font-size: 2.5rem; 
    color: #007bff; 
    margin-bottom: 20px; 
  }
  
  .benefits {
    background-color: #fff; 
    padding: 20px; 
    border-radius: 8px; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
  }
  
  .benefits h2 {
    font-size: 1.5rem; 
    color: #333; 
  }
  
  .conf {
    margin-top: 30px;
    font-size: 1.25rem; 
  }
  
  .conf a {
    color: #007bff; 
    text-decoration: none;
  }
  
  .conf a:hover {
    text-decoration: underline; 
  }
  .card {
    width: 100% !important;
    text-decoration: none;
    border: none !important;
    border-radius: 0% !important;
    box-shadow: 0 0 4px 0 rgba(57, 78, 234, 0.1)
}



.card:hover {
    border-radius: 0% !important;
    text-decoration: none;
    box-shadow: 0 0 12px 0 rgba(57, 78, 234, 0.1)
}

.image-box {
    position: relative;
    margin: auto;
    overflow: hidden;
    width: 100% !important;
}
.image-box img {
    width: 150px !important;
    transition: all 0.3s;
    display: block;
    height: 250px !important;
    transform: scale(1);
}
.image-box:hover img {
    transform: scale(2.1)!important;
}
.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  }
  .section-title h2 {
    font-size: 30px;
    font-weight: 600;
    position: relative;
    display: inline-block;
    margin: 0;
    text-transform: uppercase;
}
.section-title h2:before {
    position: absolute;
    top: 17px;
    left: -100px;
    width: 80px;
    height: 2px;
    content: "";
    background-color: #000;
}
.section-title h2:after {
    position: absolute;
    top: 17px;
    right: -100px;
    width: 80px;
    height: 2px;
    content: "";
    background-color: #000;
}